import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { DeviceGridExampleGuard } from './device-grid-example-guard';

@Injectable()
export class DeviceGridExampleNavigationFactory implements NavigatorNodeFactory {
  nav: NavigatorNode;

  constructor(private deviceGridExampleGuard: DeviceGridExampleGuard) {}

  async get() {
    if (!this.nav && this.deviceGridExampleGuard.canActivate(null, null)) {
      this.nav = new NavigatorNode({
        label: gettext('Grids'),
        icon: 'table'
      });
      this.nav.add(
        new NavigatorNode({
          label: gettext('Device grid'),
          path: '/grids/device-grid',
          icon: 'table'
        })
      );
    }
    return this.nav;
  }
}
